/* Additional CSS for specific components */

/* Home Page */
.hero {
  background-color: #007acc;
  color: white;
  padding: 4rem 2rem;
  text-align: center;
  margin-bottom: 2rem;
  border-radius: 5px;
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.hero h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  font-weight: normal;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.involvement-list {
  margin-left: 2rem;
  margin-bottom: 1.5rem;
}

/* Events Page */
.event-card {
  background: #fff;
  border-left: 4px solid #007acc;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  border-radius: 0 5px 5px 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.event-date, .event-location {
  color: #666;
  margin-bottom: 0.5rem;
}

.featured-event {
  border-left: 4px solid #007acc;
}

.event-details {
  display: flex;
  flex-wrap: wrap;
}

.event-info {
  flex: 1;
  min-width: 250px;
  padding-right: 2rem;
}

.event-description {
  flex: 2;
  min-width: 300px;
}

.event-description h3 {
  margin: 1rem 0 0.5rem;
  color: #007acc;
}

.past-event {
  opacity: 0.8;
  border-left: 4px solid #999;
}

/* Meetings Page */
.schedule {
  margin-top: 1.5rem;
}

.schedule-item {
  display: flex;
  margin-bottom: 1.5rem;
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.date {
  background: #007acc;
  color: white;
  padding: 1.5rem;
  font-weight: bold;
  min-width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content {
  padding: 1.5rem;
  flex-grow: 1;
}

.content h3 {
  margin-bottom: 0.5rem;
  color: #333;
}

/* Gallery Page */
.gallery-intro {
  margin-bottom: 2rem;
  text-align: center;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
}

.gallery-item {
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.gallery-item:hover {
  transform: translateY(-5px);
}

.gallery-image {
  width: 100%;
  height: auto;
  display: block;
}

.gallery-caption {
  padding: 1rem;
}

.gallery-caption h3 {
  margin-bottom: 0.5rem;
  color: #007acc;
}

/* Executive Board Page */
.board-intro {
  margin-bottom: 2rem;
  text-align: center;
}

.board-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

.board-member {
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.member-image {
  width: 100%;
  height: auto;
  display: block;
}

.member-info {
  padding: 1.5rem;
}

.member-position {
  color: #007acc;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.member-details {
  color: #666;
  margin-bottom: 1rem;
}

/* Contact Page */
.contact-info {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}

.contact-method {
  flex: 1;
  min-width: 250px;
  margin-bottom: 1.5rem;
  padding-right: 1.5rem;
}

.contact-method h3 {
  margin-bottom: 0.5rem;
  color: #007acc;
}

.social-media {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.social-link {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  background: #f5f5f5;
  border-radius: 4px;
  transition: background 0.3s;
}

.social-link:hover {
  background: #e5e5e5;
}

.contact-form, .subscribe-form {
  max-width: 600px;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.form-group input, .form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-family: inherit;
  font-size: 1rem;
}

.form-group textarea {
  resize: vertical;
}

@media screen and (max-width: 768px) {
  .event-details, .contact-info {
    flex-direction: column;
  }
  
  .event-info, .contact-method {
    padding-right: 0;
  }
  
  .schedule-item {
    flex-direction: column;
  }
  
  .date {
    padding: 0.75rem;
    min-width: auto;
  }
}